.back{
    background-color: #d44439;
    height: 330px;
    width: 100vw;
    z-index: -1;
    position: absolute;
    top:-30vh;
}
.card{
    display: inline-block;
    overflow: hidden;
    img{
        width:calc( 100vw - 20px );
        margin: 0 10px;
        border-radius: 8px;
    }
}
.content{
    -webkit-transition: all 0 ease;
    -o-transition: all 0 ease;
    transition: all 0 ease;
    width: 100vw;
}
.listcontainer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    >div{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        position: relative;
        width: 32vw;
        img{
            border-radius: 3px;
            width: calc(33vw - 10px);
            height: calc(33vw - 10px);
        }
        .count{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: row;
                flex-direction: row;
            position: absolute;
            height: 20px;
            right: 10px;
            color: #f1f1f1;
            top:2px;
            font-size: 10px;
            font-weight: 400;
            >img{
                padding: 2px;
                height: 12px;
                width: 12px;
            }
        }
        .title{
            text-align: left;
            padding: 6px 4px 2px 2px;
            height: 42px;
            overflow: hidden;
        }
    }
}
.recommend-list{
    h2{
        text-align: left;
        padding: 0 5px;
        border-left: 2px solid #d33a31;
    }
}
.slide{
    width: inherit;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.rankcontainer{
    width: 100vw;
    .cover{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-negative:0;
            flex-shrink:0;
        width: 100px;
        height: 100px;
        img{
            width: 100%;
            height: 100%;
        }
    }
    .rank{
        padding: 0 10px;
        >li{
            padding: 3px 0;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: row;
                flex-direction: row;
            border-bottom: 1px solid #e4e4e4;
            overflow: hidden;
        }
    }
    .song{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-negative:1;
            flex-shrink:1;
        -ms-flex-direction: column;
            flex-direction: column;
        padding: 0 20px;
        >li{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-pack: center;
                justify-content: center;
            height: .9rem;
            display: flex;
            -ms-flex-direction: column;
                flex-direction: column;
            -ms-flex-align: start;
                align-items: flex-start;
            white-space: nowrap;
            overflow: hidden;
            -o-text-overflow: ellipsis;
               text-overflow: ellipsis;
        }
    }
}

.listcontent{
    display: -ms-flexbox;
    display: flex;
    height: 60vh;
    width: 100vw;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    color: #d44439;
    font-size: 20px;
}


