.main{
    width: 100%;
}
.welcome{
    position: relative;
    height: 100vh;
    width: 100vw;
    img:nth-child(1){
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
    }
    img:nth-child(2){
        position: absolute;
        top: 0;
        height: 100vh;
        left: 0;
        z-index: -1;
    }
    img:nth-child(3){
        position: absolute;
        bottom: 0;
        width: 100%;
        left: 0;
    }
}


